body{
    background-color: #000;
    color: 	#00FF00;
    font-size: 12px;
    
}
.all-body{
    height: 100%;
}
ul{
    list-style-type: none;
    padding-left: 0;
}
.top {
    padding: 10px 0;
    border: 1px solid #00FF00; 
    background: #00FF00;  
    color: #000;
    font-family: "Lucida Console";
}

.top a{
    color: #000;
}
.users{
    border: 1px solid #00FF00;
    border-top: 0;
    height: 560px;
    padding-top: 10px;
    overflow: auto;
    font-family: "Lucida Console";
}

.users ul li{
    margin-bottom: 2px;
    padding-left: 4px;
}

.users ul li button{
    background: #000;
    border: 0;
    color:#9CDCFE;
}

.users ul li button:hover{
    text-decoration: underline;
}

.active{
    height: 8px;
    width: 8px;
    background: #00FF00;
    margin-right: 7px;
    display: inline-block;
    border-radius: 50%;
}

.inactive{
    height: 8px;
    width: 8px;
    background: #ff0066;
    margin-right: 7px;
    display: inline-block;
    border-radius: 50%;
}

.control-single{
    margin:0 5px ;
}

.chat{
    height: 560px;
    border-bottom: 1px solid #00FF00;
    border-right: 1px solid #00FF00;
    border-left: 1px solid #00FF00;
    padding: 0;
}

.chat-area{
    border-bottom: 1px solid #00FF00;
    height: 77%;
    padding: 10px;
    /* padding-bottom:0px ; */
    overflow: auto;
}

.client-msg{
    margin-bottom: 15px;
}
.msg{
    margin-left: 15px;
    margin-top: 5px;
}

.input-field textarea{
    width: 100%;
    height: 65px;
    background-color: #000;
    border: 0;
    border-bottom: 1px solid #00FF00;
    color: #00FF00;
    padding: 8px;
    font-family: "Lucida Console"
}



.send-button button{
    color: #000;
    background: #00FF00;
    border: 1px solid #00FF00;
    padding: 10px;
    font-size: 15px;
    margin-top: 5px;  
    width: 80px;  
    font-weight:bold;
    transition: ease-out .2s;
    font-family: "Lucida Console";
}

.send-button button:hover{
    background: #000;
    color: #00ff00;
    transition: ease-out .2s;
}

.client-name{
    color: #ff0066;
    margin-right: 5px;
}

.client-msg-time{
    font-style: italic;
    color: blueviolet;
}

.top-link{
    background: #00FF00;
    border: 0;
    color:#000;
}
.top-link:hover{
    text-decoration: underline;
}