.col-centered{
    margin: 0 auto;
    float: none;
}
.heading{
    font-family: "Lucida Console";
}

.register{
    margin-bottom: 10px;
    font-family: "Lucida Console";
}
.register input{
    background: #000;
    border:none ;
    border-bottom: 1px solid #00ff00;
    color: #00ff00;
    width: 230px;
    font-size: 18px;
    padding: 5px;
}

.register select{
    background: #000;
    border:none ;
    border-bottom: 1px solid #00ff00;
    color: #00ff00;
    width: 230px;
    font-size: 18px;
    padding: 5px;
}

.register-btn{
    color: #000;
    background: #00FF00;
    border: 1px solid #00FF00;
    padding: 10px;
    font-size: 15px;
    margin-top: 5px;  
    width: 80px;  
    font-weight:bold;
    transition: ease-out .2s;
}

.register-btn:hover{
    background: #000;
    color: #00ff00;
    transition: ease-out .2s;
}

.home-link{
    display: block;
    background: #000;
    border: 0;
    color:#00FF00;
    font-family: "Lucida Console";
    
    left: 0;
    right: 0;
    margin: auto;
    margin-top: 20px;
}

.home-link:hover{
    text-decoration: underline;
}